/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */
img {
	max-width: 100%;
	object-fit: cover;
}

.btn-wrap { font-weight: 700; display: inline-flex; align-items: center; gap: 1rem; }
.btn-wrap i.ph-arrow-right { width: 2rem; height: 2rem; border-radius: 50%; border: 1px solid var(--purple); flex-shrink: 0; display: flex; align-items: center; justify-content: center; transition: background-color 0.3s, transform 0.3s; }
.btn-wrap:hover {  }
.btn-wrap:hover span { text-decoration: underline; }
.btn-wrap:hover i.ph-arrow-right { transform: translateX(0.25rem); color: #fff; background: var(--purple); }

.btn-wrap.variant-primary { color: var(--purple); }
.btn-wrap.variant-primary i.ph-arrow-right { border-color: var(--purple); }
.btn-wrap.variant-primary:hover i.ph-arrow-right { background: var(--purple); }

.btn-wrap.variant-secondary { color: var(--creme); }
.btn-wrap.variant-secondary i.ph-arrow-right { border-color: var(--creme); }
.btn-wrap.variant-secondary:hover i.ph-arrow-right { color: #000; background: var(--creme); }

.btn-wrap.variant-tertiary { color: #fff; }
.btn-wrap.variant-tertiary i.ph-arrow-right { border-color: #fff; }
.btn-wrap.variant-tertiary:hover i.ph-arrow-right { color: #000; background: #fff; }
